@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600&family=Outfit:wght@100;300;400;500;600;700;800&family=Poppins:wght@500&family=Roboto:ital,wght@0,400;0,500;0,700;1,900&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
    transition: all .2s linear;
    outline: none;
}
html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body{
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    background-color: #000;
}
.btn{
    padding: 1.2rem 3.2rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    background: transparent;
    color: #fff;
    border: .1rem solid #fff;
    cursor: pointer;
    &:hover{
        background: rgba(255,255,255,.2);
    }
}
.btn-light{
    background: rgba(255,255,255,.2);
}

h1,h4,p{
    color: #fff;
}

// NAVBAR........................................................

.header{
    position: fixed;
    height: 7rem;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    // overflow: hidden;
  navbar{
    display: flex;
    height: 9vh;
    background-color: transparent;
    align-items: center;
    justify-content: space-between;
    padding-top: 1rem;
    .logo {
        margin-left: 2rem;
        a{
            color: #fff;
            text-decoration: none;
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
    .icon{
        display: none;
        @media screen and (max-width: 968px) {
            display: block;
            color: #fff;
            font-size: 2.4rem;
            margin-right: 4rem;
            cursor: pointer;
        }
    }
    .nav-menu{
        display: flex;
        justify-content: flex-end;
        list-style-type: none;
        width: 70vw;
        @media screen and (max-width:968px) {
            flex-direction: column;
            // display: flex;
            background-color: rgba(0,0,0,.9);
            // background-color: #f9004d;
            width: 100%;
            position: absolute;
            padding: 1rem 0;
            top: 0rem;            
            text-align: center;
            height: 100vh;
            z-index: -1;
            // padding-top: 8rem!important;
            left: -100%;
            padding-bottom: 20rem;
        }
        
        .nav-item{
          padding: 0 2rem;
            @media screen and (max-width: 968px) {
               padding: 2rem 0;
               margin-left: -2rem;
               &:hover{
                 background-color: #191919;
               }
              }
        }
        .nav-item a{
            text-decoration: none;
            color: #fff;
            font-size: 1.6rem;
            &:hover{
              color: #f9004d; 
                @media screen and (max-width: 968px) {
                  border: none;
                }        
            }
            @media screen and (max-width: 968px) {
              font-size: 3rem;
            //   padding-top: -5rem;
             }
        }
    }
    .nav-menu.active{
        @media screen and (max-width: 968px) {
            left: 0;
            transition: all .5s ease-in-out;
        }
    }
    @media screen and (max-width: 968px) {
        padding-bottom: 1.5rem;
    }
  }
}
.header-bg{
  background-color: rgba(0,0,0,.85);
  transition: .5s;
}




// HOME.............................................................


.hero{
    height: 100vh;
    width: 100%;
    object-fit: contain;
    #video{
        position: fixed;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
    }
    .content{
        text-align: center;
        position: absolute;
        // margin: auto;
        width: 100%;
        height: 100%;
        top: 40vh;
        h1{
            font-size: 4rem;
            color: #fff;
            @media screen and (max-width: 640px) {
                font-size: 3rem;
            }
        }
        p{
            font-size: 1.8rem;
            font-weight: 200;
            text-transform: uppercase;
            margin-bottom: 1.6rem;
            color: #fff;
            @media screen and (max-width: 640px) {
                font-size: 1.4rem;
                margin-bottom: 1.6rem;
            }
        }
        .btn{
            margin: 1rem 1rem;
            font-size: 1.4rem;
            &:hover{
                color: #fff;
            }
        }
    }
}


// Footer.....................................................

.footer{
    width: 100%;
    padding: 6rem 0;
    background: rgba(0,0,0,.8);
    .footer-container{
        max-width: 114rem;
        margin: auto;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 4rem;
        .left{
            height: 100%;
            max-width: 100%;
            display: flex;
            flex-direction: column;
            padding: 1rem;
            h4{
                font-size: 1.8rem;
                padding: 1rem 0;
            }
            p{
                font-size: 1.6rem;
                padding: 1rem 0;
            }
            .location{
                display: flex;
                align-items: center;
                p{
                    padding-bottom: .5rem;
                    font-size: 1.8rem;
                    padding-left: 1rem;
                }
                h4{
                    padding-top: 0;
                }
                @media screen and (max-width: 640px) {
                    text-align: center;
                    align-items: center;
                    justify-content: center;
                    padding-right: 3rem;
                }
            }
        }
        .right{
            height: 100%;
            max-width: 100%;
            display: flex;
            flex-direction: column;
            padding: 1rem;
            margin: auto;
            h4{
                font-size: 3rem;
            }
            p{
                font-size: 1.7rem;
                padding-top: 1rem;
            }
            .social{
                padding-top: 1rem;
                @media screen and (max-width: 640px) {
                    margin: auto;
                }
            }
        }
        @media screen and (max-width: 640px) {
            grid-template-columns: 1fr;
        }
    }
    @media screen and (max-width: 640px) {
        text-align: center;
    }
}


// HEROIMG.......................................................................



.hero-img{
    background: rgba(0,0,0,.3);
    height: 50vh;
    width: 100%;
    position: relative;
    &::before{
        content: "";
        background: url(./component/images/heroImg.jpg) no-repeat center center/cover;
        height: 100%;
        width: 100%;
        // background-position: center;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
    .heading{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h1{
            font-size: 3.4rem;
            @media screen and (max-width: 640px) {
                font-size: 2rem;
            }
        }
        p{
            font-size: 2rem;
        }
    }
}


// PRICING..............................................................

.pricing{
    width: 100%;
    padding: 6rem 1rem;
    .card-container{
        max-width: 114rem;
        margin: auto;
        // display: grid;
        grid-gap: 4rem;
        // grid-template-columns: repeat(3, 1fr);
        .card{
            border: .1rem solid #eee;
            background-color: #000;
            color: #eee;
            text-align: center;
            padding: 1rem;
            &:hover{
                background-color: #141414;
            }
            h3{
                font-size: 2.5rem;
                padding: 1rem;
            }
            .bar{
                border-bottom: .1rem solid #eee;
                width: 20%;
                margin: 1.5rem auto;
                display: block;
            }
            .btc{
                font-size: 4rem;
                font-weight: 600;
                padding: 1rem;    
            }
            p{
                color: #eee;
                padding: 1rem 0;
                font-size: 1.6rem;
            }
            .btn{
                display: block;
                width: 80%;
                margin: 2rem auto;
                font-size: 1.6rem;
                &:hover{
                    color: #fff;
                }
            }
        }
        @media screen and (max-width: 1200px) {
            // max-width: 100%;
        }
        @media screen and (max-width: 992px) {
            max-width: 90%;
        }
        @media screen and (max-width: 550px) {
            max-width: 100%;
        }
        @media screen and (max-width: 400px) {
            max-width: 110%;
        }
    }
}


// TRAINING........................................................................


.training{
    width: 100%;
    margin: 3rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 100%;
    .left{
        text-align: center;
        margin: auto;
        padding: 1rem 4rem;
        max-width: 60rem;
        h1{
            font-size: 4rem;
        }
        p{
            font-size: 1.8rem;
            margin: 1.2rem 0;
        }
        .btn{
            &:hover{
                color: #fff;
            }
        }
    }
    .right{
        width: 100%;
        .img-container{
            display: grid;
            grid-template-columns: repeat(12, 1fr);
            position: relative;
            align-items: center;
            text-align: center;
            .img{
                max-width: 60%;
                border: .1rem solid #333;
                @media screen and (max-width: 1140px) {
                    max-width: 80%;
                }
            }
        }
        .top{
            grid-column: 1 / span 8;
            grid-row: 1;
            padding-top: 20%;
            z-index: 6;
        }
        .bottom{
            grid-column: 4/ -1;
            grid-row: 1;
            z-index: 5;
        }
    }
    @media screen and (max-width: 1140px) {
        grid-template-columns: 1fr;
    }
}




// CONTACT.....................................................................................


form{
    display: flex;
    flex-direction: column;
    padding: 1rem;
    margin: auto;
    max-width: 60rem;
    label{
        margin-bottom: .5rem;
        color: #eee;
        font-size: 1.7rem;
    }
    input,textarea{
        margin-bottom: 1rem;
        padding: 1rem 1rem;
        font-size: 1.4rem;
        font-family: "outfit", sans-serif;
        background: rgba(255,255,255,.1);
        border-color: rgba(255,255,255,.1);
        color: #f4f4f4;
    }
    .btn{
        font-size: 1.7rem;
        &:hover{
            color: #fff;
        }
    }
}




















